import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import My_router from '../router/My_router';
class SignIn extends Component {
  render() {
    return (
      <main className="page-content sign-page">
        <div className="layout">
          <div className="breadcrumbs">
            <Link to={My_router.url("HomePageDefault")} >{My_router.stor.page[My_router.stor.lang].SignIn.r1  }</Link> &gt;
            <span></span>
          </div>
          <div className="page-title">{My_router.stor.page[My_router.stor.lang].SignIn.r2  }</div>
          <div className="box">
            <form>
              <div className="rows">
                <div className="row">
                  <label htmlFor="email">{My_router.stor.page[My_router.stor.lang].SignIn.r3  }</label><br />
                  <input type="email" id="email" placeholder={My_router.stor.page[My_router.stor.lang].SignIn.r10  } />
                </div>
                <div className="row">
                  <div className="one-row">
                    <label htmlFor="password">{My_router.stor.page[My_router.stor.lang].SignIn.r3  }</label>
                    <a href="javascript:void(0);" className="forgot-password">{My_router.stor.page[My_router.stor.lang].SignIn.r4  }</a>
                    <div className="clear" />
                  </div>
                  <input type="password" id="password" placeholder={My_router.stor.page[My_router.stor.lang].SignIn.r5  } />
                </div>
              </div>
              <button type="submit" className="button">{My_router.stor.page[My_router.stor.lang].SignIn.r6  }</button>
              <div className="under-button">
              {My_router.stor.page[My_router.stor.lang].SignIn.r7  }
              </div>
              <div className="register">
              {My_router.stor.page[My_router.stor.lang].SignIn.r8  } <Link to={My_router.url("SignUp")}  >{My_router.stor.page[My_router.stor.lang].SignIn.r9  }</Link>
              </div>
            </form>
          </div>
        </div>
      </main>
    );
  }
}

export default SignIn;



