import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import My_router from '../router/My_router';
class howThisWorks extends Component {
  render() {
    return (
      <main className="page-content how-this-works-page">
        <div className="layout">
          <div className="layout-mid">
            <div className="breadcrumbs mobile-padding">
              <Link to={My_router.url("HomePageDefault")} >{My_router.stor.page[My_router.stor.lang].howThisWorks.r1}</Link> &gt;
            <span>{My_router.stor.page[My_router.stor.lang].howThisWorks.r2}</span>
            </div>
            <div className="page-title mobile-padding">{My_router.stor.page[My_router.stor.lang].howThisWorks.r2}</div>
            <div className="steps mobile-padding">
              <div className="step video">
                <div className="content">
                  <div className="iframe-wrapper">
                    <iframe width={570} height={321} src="https://www.youtube.com/embed/0r6C3z3TEKw" frameBorder={0} allowFullScreen />
                  </div>
                  <div className="text">
                    {My_router.stor.page[My_router.stor.lang].howThisWorks.r3}
                  </div>
                </div>
              </div>
              {["","",""].map((a,b)=>{
              return (
<div className="step" key={b}>
                <div className="title">{My_router.stor.page[My_router.stor.lang].howThisWorks.r4} - 1</div>
                <div className="content">
                  <div className="image-wrapper">
                    <img src="pictures/how-this-works/group.png" srcSet="pictures/how-this-works/group@2x.png 2x, pictures/how-this-works/group@3x.png 3x" />
                  </div>
                  <div className="text">
                    {My_router.stor.page[My_router.stor.lang].howThisWorks.r3}
                  </div>
                </div>
              </div>
              )
            })}

          </div>
          </div>
        </div>
      </main>
    );
  }
}

export default howThisWorks;



