import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import ReactDOM from 'react-dom';
import My_router from '../router/My_router';
class footer extends Component {
  render() {
    return (
      <footer>
        <div className="layout">
          <div className="layout-mid">
            <div className="text">
              © SiteName 2014-2015<br />
              {My_router.stor.page[My_router.stor.lang].footer.r1}
            </div>
            <div className="links">
              <div className="link">
                <Link to={My_router.url("DirectForm")} >{My_router.stor.page[My_router.stor.lang].footer.r2}</Link>
              </div>
              <div className="link">
                <Link to={My_router.url("Cooperation")} >{My_router.stor.page[My_router.stor.lang].footer.r3  }</Link>
              </div>
              <div className="link">
              <Link to={My_router.url("News")} >{My_router.stor.page[My_router.stor.lang].footer.r4}</Link>
              </div>
              <div className="link">
              <Link to= {My_router.url("Contacts")}>{My_router.stor.page[My_router.stor.lang].footer.r5  }</Link>
              </div>
              <div className="link">
              <Link to={My_router.url("faq")} >{My_router.stor.page[My_router.stor.lang].footer.r6  }</Link>
              </div>
            </div>
            <div className="clear" />
          </div>
        </div>
      </footer>
    );
  }
}

export default footer;



