import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import My_router from '../router/My_router';
class SignUp extends Component {
  render() {
    return (
      <main className="page-content sign-page">
      <div className="layout">
        <div className="breadcrumbs">
          <Link to={My_router.url("HomePageDefault")} >{My_router.stor.page[My_router.stor.lang].SignUp.r1  }</Link> &gt;
          <span>{My_router.stor.page[My_router.stor.lang].SignUp.r  }</span>
        </div>
        <div className="page-title">{My_router.stor.page[My_router.stor.lang].SignUp.r2  }</div>
        <div className="box">
          <form>
            <div className="rows">
              <div className="row">
                <label htmlFor="email">{My_router.stor.page[My_router.stor.lang].SignUp.r3 }</label><br />
                <input type="email" id="email" placeholder={My_router.stor.page[My_router.stor.lang].SignUp.r16  } />
              </div>
              <div className="row">
                <div className="one-row">
                  <label htmlFor="password">{My_router.stor.page[My_router.stor.lang].SignUp.r4  }</label>
                  <a href="javascript:void(0);" className="forgot-password">{My_router.stor.page[My_router.stor.lang].SignUp.r5  }</a>
                  <div className="clear" />
                </div>
                <input type="password" id="password" placeholder={My_router.stor.page[My_router.stor.lang].SignUp.r6  } />
              </div>
              <div className="row">
                <label htmlFor="password1">{My_router.stor.page[My_router.stor.lang].SignUp.r7  }</label><br />
                <input type="password" id="password1" placeholder={My_router.stor.page[My_router.stor.lang].SignUp.r8  } />
              </div>
            </div>
            <div className="captcha">
              <div className="text">
              {My_router.stor.page[My_router.stor.lang].SignUp.r9[0]  }<br />{My_router.stor.page[My_router.stor.lang].SignUp.r9[1]}
              </div>
              <div className="recaptcha">
                recaptcha
              </div>
              <div className="clear" />
            </div>
            <div className="agreement">
              <input type="checkbox" />
              {My_router.stor.page[My_router.stor.lang].SignUp.r12  } <a href="javascript:void(0);">{My_router.stor.page[My_router.stor.lang].SignUp.r10  }</a>
            </div>
            <button type="submit" className="button">{My_router.stor.page[My_router.stor.lang].SignUp.r13  }</button>
            <div className="register">
            {My_router.stor.page[My_router.stor.lang].SignUp.r15  } <a href="javascript:void(0);">{My_router.stor.page[My_router.stor.lang].SignUp.r14  }</a>
            </div>
          </form>
        </div>
      </div>
    </main>
    );
  }
}

export default SignUp;



