import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import My_router from '../router/My_router';
class News extends Component {
  render() {
    return (
      <main className="page-content news-page">
        <div className="layout">
          <div className="layout-mid">
            <div className="breadcrumbs mobile-padding">
              <Link to={My_router.url("HomePageDefault")} >{My_router.stor.page[My_router.stor.lang].News.r1  }</Link> &gt;
              <span>{My_router.stor.page[My_router.stor.lang].News.r2  }</span>
            </div>
            <div className="page-title mobile-padding">{My_router.stor.page[My_router.stor.lang].News.r2  }</div>
          </div>
        </div>
        <div className="layout mobile-padding">
          <div className="layout-mid">
            <div className="news-list">
              { 
              
                ["","",""].map((a,b) => {
                console.log("News");
                
                return (
                  <div className="item" key={b}>
                    <div className="image">
                      <img src="pictures/news/item.png" />
                    </div>
                    <div className="content">
                      <div className="date">
                        <svg xmlns="http://www.w3.org/2000/svg" width={22} height={23} viewBox="0 0 22 23">
                          <path fill="#B4B4B7" fillRule="nonzero" d="M4 0v1H0v22h22V1h-4V0h-2v1H6V0H4zM2 3h2v1h2V3h10v1h2V3h2v2H2V3zm0 4h18v14H2V7zm6 2v2h2V9H8zm4 0v2h2V9h-2zm4 0v2h2V9h-2zM4 13v2h2v-2H4zm4 0v2h2v-2H8zm4 0v2h2v-2h-2zm4 0v2h2v-2h-2zM4 17v2h2v-2H4zm4 0v2h2v-2H8zm4 0v2h2v-2h-2z" />
                        </svg>
                        
                  </div>
                      <div className="title">
                      {My_router.stor.page[My_router.stor.lang].News.r3  }
                  </div>
                      <div className="text">
                      {My_router.stor.page[My_router.stor.lang].News.r4  }
                  </div>
                      <div className="more-info">
                        <a href="javascript:void(0);" className="more-info">{My_router.stor.page[My_router.stor.lang].News.r5  } &gt;</a>
                      </div>
                    </div>
                    <div className="clear" />
                  </div>
                )
              })}

            </div>
            <div className="more-items">
              <a href="javascript:void(0);" className="button white">{My_router.stor.page[My_router.stor.lang].News.r6  }</a>
            </div>
          </div>
        </div>
      </main>
    );
  }
}

export default News;



