import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import My_router from './router/My_router';
///////////Component
import Article from './page/Article.js';
import Contacts from './page/Contacts.js';
import Cooperation from './page/Cooperation.js';
import DirectForm from './page/DirectForm.js';
import Faq from './page/faq.js';
import FaqAnswer from './page/faqAnswer.js';
import Fees from './page/Fees.js';
import HomePageDefault from './page/HomePageDefault.js';
import HowThisWorks from './page/howThisWorks.js';
import Htmltojsx from './page/htmltojsx.js';
import News from './page/News.js';
import Payment from './page/Payment.js';
import Reviews from './page/Reviews.js';
import SignIn from './page/SignIn.js';
import SignUp from './page/SignUp.js';
// element
import Header from './heder/header.js';
import Footer from './footer/footer.js';
class App extends Component {
    //     componentDidMount() {
    //         // this.props.match.url
    //         // console.log(this,"this"); 
    //         // My_router(this)
    //   }
    //   componentWillUnmount(){
    //     console.log("componentWillUnmount");
    //   }
    render() {
        let url = this.props.match.url
        console.log(My_router.url(url));

        return (
            <Router>
                <div>
                    <Route exact path="/*" component={Header} />
                    <Switch>
                        <Route exact path="/Article" component={Article} />
                        <Route exact path="/Contacts" component={Contacts} />
                        <Route exact path="/Cooperation" component={Cooperation} />
                        <Route exact path="/DirectForm" component={DirectForm} />
                        <Route exact path="/Faq" component={Faq} />
                        <Route exact path="/FaqAnswer" component={FaqAnswer} />
                        <Route exact path="/Fees" component={Fees} />
                        <Route exact path="/" component={HomePageDefault} />
                        <Route exact path="/HowThisWorks" component={HowThisWorks} />
                        <Route exact path="/Htmltojsx" component={Htmltojsx} />
                        <Route exact path="/Htmltojsx" component={Htmltojsx} />
                        <Route exact path="/News" component={News} />
                        <Route exact path="/Payment" component={Payment} />
                        <Route exact path="/Reviews" component={Reviews} />
                        <Route exact path="/SignIn" component={SignIn} />
                        <Route exact path="/SignUp" component={SignUp} />
                    </Switch>
                    <Switch>
                        <Route exact path="/*" component={Footer} />
                    </Switch>
                </div>
            </Router>
        );
    }
}

export default App;





