import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import My_router from '../router/My_router';
class Reviews extends Component {
  render() {
    return (
      <main className="page-content reviews-page">
        <div className="layout">
          <div className="layout-mid">
            <div className="breadcrumbs mobile-padding">
              <Link to={My_router.url("HomePageDefault")} >{My_router.stor.page[My_router.stor.lang].Reviews.r9  }</Link> &gt;
              <span>{My_router.stor.page[My_router.stor.lang].Reviews.r10  }</span>
            </div>
            <div className="page-title mobile-padding">{My_router.stor.page[My_router.stor.lang].Reviews.r10  }</div>
          </div>
        </div>
        <div className="layout mobile-padding">
          <div className="layout-mid">
            <div className="with-sides">
              <div className="side-left">
                <div className="reviews-list">
                  {["", "",""].map((a,b) => {
                    return (
                      <div className="item box" key={b}>
                        <div className="title">
                        {My_router.stor.page[My_router.stor.lang].Reviews.r1  }
                    </div>
                        <div className="text">
                        {My_router.stor.page[My_router.stor.lang].Reviews.r2  }
                    </div>
                      </div>
                    )
                  })}

                </div>
                <div className="more-items">
                  <a href="javascript:void(0);" className="button white">{My_router.stor.page[My_router.stor.lang].Reviews.r3  }</a>
                </div>
              </div>
              <div className="side-right">
                <div className="black-white-list form">
                  <div className="box">
                    <div className="header">{My_router.stor.page[My_router.stor.lang].Reviews.r4  }</div>
                    <div className="content">
                      <form>
                        <input type="text" placeholder={My_router.stor.page[My_router.stor.lang].Reviews.r5  } />
                        <input type="text" placeholder={My_router.stor.page[My_router.stor.lang].Reviews.r6  } />
                        <textarea placeholder={My_router.stor.page[My_router.stor.lang].Reviews.r7  } defaultValue={""} />
                        <button type="submit" className="button">{My_router.stor.page[My_router.stor.lang].Reviews.r8  }</button>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
              <div className="clear" />
            </div>
          </div>
        </div>
      </main>
    );
  }
}

export default Reviews;



