import $ from "jquery";

document.body.classList.remove('js-disabled');
document.body.classList.add('js-enabled');

$(function() {
    var $selectList = $('select');

    // $selectList.filter(':not(.with-icon)').select2({
    //     minimumResultsForSearch: Infinity
    // });

    // $selectList.filter('.with-icon').select2({
    //     minimumResultsForSearch: Infinity,
    //     templateResult: formatState,
    //     templateSelection: formatState
    // });

    // Главная страница
    if ($('.page-content.home-page').length == 1) {
        // Вывод форм при смене select'а на мобиле
        if (isMobile()) {
            $selectList.on('select2:select', function(e) {
                var $selects = $('.exchange .box .mobile-select select');
                var rateLeft = $($selects[0]).val();
                var rateRight = $($selects[1]).val();

                if (!rateLeft || !rateRight) {
                    return;
                }

                $('input[name="exchange-left-currency"]#exchange-' + rateLeft + '-to-' + rateRight).prop('checked', true);
                $('.exchange').addClass('mobile-visible');
            });
        }

        // Scroll down
        $('.js-scroll-down').on('click', function(e) {
            var $scrollbar = $(e.target).closest('.tab-content').find('.scrollbar');
            $scrollbar.scrollTop($scrollbar.scrollTop() + 20);
        });

        // Focus
        $('.exchange .box input[type="radio"]').on('change', function() {
            setTimeout(function() {
                $('.exchange .exchange-form:visible input:visible').first().focus();
            }, 200);
        });

        // Поиск
        $('.exchange .search-currency input').keyup(function() {
            var $this = $(this),
                search = $this.val().toLowerCase(),
                $tabContent = $this.closest('.tab-content'),
                $items = $tabContent.find('.items .item'),
                visible = false;

            $items.each(function(i, item) {
                var $item = $(item),
                    itemVisible = $item.find('.title').text().toLowerCase().indexOf(search) != -1;

                $item.toggle(itemVisible);

                if (itemVisible) {
                    visible = true;
                }
            });

            $tabContent.toggleClass('search-result-empty', !visible);
            toggleScrollDown();
        });

        toggleScrollDown();
    }

    // Скрываем выбор языка
    $(document).on('click', function(e) {
        if ($(e.target).closest('.lang').length == 0) {
            $('#header-lang').prop('checked', false);
        }
    });

    function isMobile() {
        return window.matchMedia('(max-width: 767px)').matches;
    }

    function toggleScrollDown() {
        var $tabContent = $('.tab-content:visible');

        $tabContent.each(function(i, tab) {
            var $tab = $(tab),
                $scrollbar = $tab.find('.scrollbar');;

            $tab.find('.scroll-down').toggle($scrollbar.height() < $scrollbar.find('.items').height());
        });
    }

    function formatState(state) {
        if (!state.id) {
            return state.text;
        }

        var baseUrl = 'pictures/logos/';
        var $state = $(
            '<span><img src="' + baseUrl + '/' + state.element.value.toLowerCase() + '.png" class="img-flag" /> <span class="text">' + state.text + '</span></span>'
        );
        return $state;
    };
});



